.stack-logo-container {
    background: white;
    position: relative;
    width: 90%;
    height: auto;
    aspect-ratio: 1/1;
    border: solid 0.05em #ccc;
    border-radius: 15%;
    margin: 5%;
    padding: 5%;
    float: left;
}
.stack-logo-container > img {
    width: 75%;
    border-radius: 15%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}
