body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
    border: 1px solid #008dd7 !important;
}

#root {
    background: linear-gradient(150deg, #0E1824 10%, #C5DEFC 100%);
}
.main {
    width: 75vw !important;
}

@media (max-width: 600px) {
    .main {
        width: 100vw !important;
    }
    .title {
        text-align: center !important;
        margin: auto !important;
    }
}

.section {
    padding: 1.8rem 1rem;
    font-family: "Quicksand";
}

.section-light {
    background: #dbe5f1;
}

.section-dark {
    background: #365f91;
    color: white;
}

.section-content {
    padding: 0 3rem !important;
}

.name {
    color: #365f91;
    font-family: "Open Sans";
    font-weight: 900;
    font-size: 4rem;
    text-align: center;
}

.title {
    color: #008dd7;
    font-family: "Courier New";
    font-weight: 900;
    font-size: 1rem;
    text-align: right;
    margin-right: 3.6rem;
}

.section-tag {
    background: #008dd7;
    display: inline-block;
    padding: 0 1.5rem;
    height: 3.2rem;
    margin-bottom: 1rem;
    border-radius: 0.6rem;
    box-shadow: rgb(0 0 0 / 50%) 0px 4px 6px;
}

.section-tag h3 {
    font-family: "Baloo Chettan";
    text-align: center;
    line-height: 3.2rem;
    color: #d8e2f1;
}

.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-style: italic;
}

.skill-group {
    display: -webkit-flex;
    display: flex;
}
.skill-group-name {
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    width: 6rem;
    font-weight: bold;
}
.skill-tags {
    -webkit-flex: 1 1;
            flex: 1 1;
    
}
.skill-tags > div {
    margin-right: 0.5rem;
}
.info-field {
    margin-bottom: 0.325rem;
}

.info-field-icon-container {
    padding: 0 !important;
    max-width: 1.3rem !important;
}

.info-field-icon-container > img {
    width: 100%;
    padding-right: 0 !important;
}

.info-field-icon-value {
    line-height: 1.5rem;
    padding: 0 !important;
    margin-left: 0.5rem;
}

.info-field-icon-value a {
    text-decoration: none;
}

.skill-container {
    display: inline-block;
    border: solid 1px rgba(255, 255, 255, 0.4);
    padding: 0 0.5rem;
    border-radius: 0.25rem;
    font-size: 0.875rem;
}


.project-avatar {
    float: left;
}
.project-avatar:hover {
    cursor: pointer;

}

.project-logo-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 6rem;
    margin: auto;
    background-color: transparent;
    -webkit-align-items: center;
            align-items: center;
}
.project-logo-container > img {
    width: 100%;
    border-radius: 0.5rem;
}
.project-logo-container:hover > img {
    box-shadow: rgb(0 0 0 / 50%) 0px 4px 6px;
}
.project-logo-container > p {
    margin-top: 0.3125rem;
    color: #007bff;
}
.project-logo-container:hover > p {
    text-decoration: underline;
}

.modal-dialog-custom .modal-content {
    background: #dbe5f1;
    font-family: "Quicksand";
}
.modal-header-custom {
    border-bottom: 1px solid #008dd7 !important;
}
.modal-title {
    width: 100%;
}
.modal-title p {
    margin-bottom: 0;
}

.modal-content p {
    margin-top: 0;
    margin-bottom: 0;
}

.project-about p {
    margin: 0;
}
.stack-logo-container {
    background: white;
    position: relative;
    width: 90%;
    height: auto;
    aspect-ratio: 1/1;
    border: solid 0.05em #ccc;
    border-radius: 15%;
    margin: 5%;
    padding: 5%;
    float: left;
}
.stack-logo-container > img {
    width: 75%;
    border-radius: 15%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}


.avatar-container {
    max-width: 18rem;
    margin: auto;
    padding: 0;
}

.avatar-container > img {
    width: 100%;
}

.duration {
    font-size: 0.75rem;
}

